/* Centered OTP Verification Layout */
.otp-center-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(135deg, #2563eb, #1e40af); /* same as login */
  font-family: "Poppins", sans-serif;
}

.otp-box {
  background: #fff;
  padding: 40px 50px;
  border-radius: 16px;
  box-shadow: 0 6px 25px rgba(0, 0, 0, 0.15);
  text-align: center;
  width: 400px;
  max-width: 90%;
  position: relative;
}

.back-link {
  position: absolute;
  top: 15px;
  left: 15px;
  background: none;
  border: none;
  color: #2563eb;
  font-size: 1rem;
  cursor: pointer;
}

.otp-box h2 {
  font-size: 1.6rem;
  color: #111827;
  margin-bottom: 10px;
}

.otp-email {
  font-size: 0.95rem;
  color: #333;
  margin-bottom: 25px;
}

.otp-inputs {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 25px;
}

.otp-inputs input {
  width: 50px;
  height: 50px;
  text-align: center;
  font-size: 1.2rem;
  border: 2px solid #2563eb;
  border-radius: 10px;
  outline: none;
  transition: all 0.2s ease;
}

.otp-inputs input:focus {
  border-color: #1e40af;
  background: #eff6ff;
}

.access-btn {
  background: linear-gradient(135deg, #2563eb, #1e40af);
  color: white;
  padding: 12px 30px;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  width: 100%;
  transition: 0.3s ease;
}

.access-btn:hover {
  background: #1e40af;
}

.resend {
  margin-top: 15px;
  font-size: 0.9rem;
}

.resend a {
  color: #2563eb;
  text-decoration: none;
  font-weight: 600;
}

/* Responsive */
@media (max-width: 480px) {
  .otp-box {
    padding: 30px 25px;
  }

  .otp-inputs input {
    width: 40px;
    height: 40px;
    font-size: 1rem;
  }

  .access-btn {
    padding: 10px;
    font-size: 0.9rem;
  }
}
