/* Login.css */

body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: 'Poppins', sans-serif;
}

.login-container {
  display: flex;
  height: 100vh;
  background: #0f172a;
  color: white;
}

/* LEFT PANEL */
.login-left {
  flex: 1;
  background: linear-gradient(180deg, #0f172a, #1e293b);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px;
}

.logo {
  text-align: center;
}
.logo img {
  width: 80px;
  margin-bottom: 10px;
}
.logo h1 {
  font-size: 28px;
  color: #38bdf8;
}

.features {
  margin-top: 40px;
  width: 100%;
  max-width: 280px;
}
.feature-box {
  background: rgba(255, 255, 255, 0.05);
  margin: 12px 0;
  padding: 12px 16px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  gap: 10px;
}

/* RIGHT PANEL */
.login-right {
  flex: 1.3;
  background: #1e293b;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px;
}

.login-form-box {
  background: #0f172a;
  padding: 40px;
  border-radius: 16px;
  width: 100%;
  max-width: 420px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.login-form-box h2 {
  font-size: 24px;
  margin-bottom: 8px;
}
.login-form-box p {
  font-size: 14px;
  color: #94a3b8;
  margin-bottom: 20px;
}

label {
  display: block;
  font-size: 14px;
  color: #fbbf24;
  margin-bottom: 6px;
}
input[type="text"],
input[type="password"] {
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 6px;
  margin-bottom: 18px;
  outline: none;
}

/* Buttons */
.btn-login {
  width: 100%;
  background: linear-gradient(90deg, #3b82f6, #7c3aed);
  border: none;
  padding: 12px;
  color: white;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  margin-top: 10px;
}

.btn-login:hover {
  background: linear-gradient(90deg, #2563eb, #6d28d9);
}

.btn-create {
  width: 100%;
  background: linear-gradient(90deg, #f97316, #84cc16);
  border: none;
  padding: 12px;
  color: white;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  margin-top: 10px;
}

.form-options {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 13px;
  margin-top: -10px;
  margin-bottom: 10px;
}

.form-options a {
  color: #60a5fa;
  text-decoration: none;
}

/* Responsive */
@media (max-width: 900px) {
  .login-container {
    flex-direction: column;
  }
  .login-left {
    display: none;
  }
  .login-right {
    flex: 1;
    width: 100%;
  }
}

/* === OTP Verification Popup === */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.popup {
  background-color: #1e293b;
  color: #fff;
  padding: 30px 40px;
  border-radius: 12px;
  text-align: center;
  width: 320px;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.4);
}

.popup-title {
  color: #facc15;
  font-size: 1.3rem;
  margin-bottom: 10px;
}

.popup button {
  background: linear-gradient(90deg, #6366f1, #8b5cf6);
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  margin-top: 15px;
  cursor: pointer;
  transition: 0.3s ease;
}

.popup button:hover {
  opacity: 0.8;
}

/* When popup is active, blur everything behind */
.login-container.blur {
  filter: blur(6px);
  transition: filter 0.3s ease;
  pointer-events: none; /* disables background clicks */
}

/* Popup overlay */
.popup-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.4); /* dark transparent backdrop */
  backdrop-filter: blur(4px); /* subtle frosted glass look */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

/* Popup box styling */
.popup {
  background: rgba(255, 255, 255, 0.9);
  padding: 40px;
  border-radius: 20px;
  text-align: center;
  width: 360px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.25);
  animation: fadeIn 0.3s ease;
}

.popup-title {
  margin-bottom: 10px;
  font-size: 1.5rem;
  color: #333;
}

.popup button {
  margin-top: 15px;
  background-color: #0078ff;
  color: white;
  border: none;
  border-radius: 10px;
  padding: 10px 25px;
  cursor: pointer;
  font-size: 1rem;
  transition: all 0.2s ease;
}

.popup button:hover {
  background-color: #005ecc;
}

/* Smooth fade animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.9);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

