.verify-popup-container {
  position: fixed;
  top: 0; left: 0;
  width: 100%; height: 100%;
  display: flex; justify-content: center; align-items: center;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1000;
}

.verify-popup {
  background: #fff;
  padding: 40px;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 4px 20px rgba(0,0,0,0.2);
  width: 350px;
}

.continue-btn {
  background: linear-gradient(90deg, #4a4aff, #7a6cf4);
  color: white;
  padding: 10px 25px;
  border: none;
  border-radius: 6px;
  margin-top: 20px;
  cursor: pointer;
}
